import {
  BMINTRALBInstanceAliasList,
  BMINTRALBInvalidDemensions,
  namespace,
  templateQueryIdMap,
  isValidMetric,
} from './query_def';
import { BaseDatasource } from '../_base/datasource';
import _ from 'lodash';

export default class DCDatasource extends BaseDatasource {
  Namespace = namespace;
  InstanceAliasList = BMINTRALBInstanceAliasList;
  InvalidDimensions = BMINTRALBInvalidDemensions;
  templateQueryIdMap = templateQueryIdMap;
  // serviceplugin.jsonconstant.tsSERVICES_API_INFO
  InstanceReqConfig = {
    service: 'bmlb',
    action: 'DescribeLoadBalancers',
    responseField: 'LoadBalancerSet',
    interceptor: {
      request: (params) => ({
        ...params,
        LoadBalancerType: 'internal',
      }), //  
    },
  };
  RegionMap = {};
  constructor(instanceSettings, backendSrv, templateSrv) {
    super(instanceSettings, backendSrv, templateSrv);
  }
  // getRegion
  getRegions() {
    return this.doRequest(
      {
        url: this.url + '/bm',
      },
      'bm',
      { action: 'DescribeRegions', region: 'ap-guangzhou' }
    ).then((response) => {
      // zone
      this.RegionMap = _.keyBy(response.RegionInfoSet, 'Region');
      return _.map(response.RegionInfoSet || [], (item) => {
        return {
          text: item.RegionDescription,
          value: item.Region,
        };
      });
    });
  }
  async getMetrics(region = 'ap-guangzhou') {
    const rawSet = await super.getMetrics(region);
    return _.compact(
      rawSet.map((item) => {
        if (isValidMetric(item)) {
          return item;
        }
        return null;
      })
    );
  }
}
