function modifyDimensons(metricItem: any) {
  const metricTmp = _.cloneDeep(metricItem);
  metricTmp.Dimensions.forEach((item) => {
    // item.Dimensions = item.Dimensions.map((v) => CLSInvalidDemensions[v] || v);
    item.Dimensions = ['uin', 'TopicId'];
  });
  return metricTmp;
}
